ísticos, respetar la diversidad biológica y los sistemas ecológicos, y promover la viabilidad económica a largo plazo. Este enfoque se basa en tres pilares: sostenibilidad económica, que asegura el desarrollo rentable y duradero; sostenibilidad ambiental, que protege el entorno natural; y sostenibilidad sociocultural, que fomenta el respeto por la cultura local y facilita un intercambio enriquecedor entre turistas y comunidades.")])])]),_c('div',{staticClass:"col-lg-5 ps-lg-0 d-none d-lg-block"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t3/img2.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row bgc-1 align-items-center p-4 py-4",attrs:{"data-aos":"fade-down"}},[_c('div',{staticClass:"col-lg-12 col-12 px-lg-5 px-4"},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-12"},[_c('div',{staticClass:"titulo-sexto color-primario"},[_c('h5',[_vm._v("Figura 5. ")]),_c('span',[_vm._v("Turismo sustentable")])])]),_c('div',{staticClass:"col-lg-7"},[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/temas/t3/figura5.svg"),"alt":"La imagen presenta un modelo circular titulado “Turismo Sustentable”, dividido en tres dimensiones clave: económica, ambiental y sociocultural. Cada una de estas dimensiones contiene varios elementos que contribuyen al turismo sostenible. En la dimensión económica, se destacan factores como la viabilidad económica, calidad del empleo y prosperidad local. La dimensión ambiental incluye la gestión del territorio, del agua, energía, residuos, conservación de la biodiversidad y la medición de huella de carbono. Por su parte, la dimensión sociocultural abarca aspectos como la protección del patrimonio, oferta cultural y bienestar de la comunidad local."}}),_c('figcaption',[_vm._v("Fuente: Rueda del turismo sostenible - Consejo Global del turismo Sostenible, apoyado por la Organización Mundial del Turismo")])])])])])},fun